@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Krona+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jura:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header{padding-top:15px;background-color:#252525;padding-bottom:30px;border-bottom-right-radius:45px;border-bottom-left-radius:45px}header .flags{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-flex-direction:row;flex-direction:row;height:35px;width:70px;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-content:center;align-content:center;right:0;margin-right:20px;margin-left:auto;border-radius:20px;background-color:white;position:relative;box-shadow:5px 5px 10px black}header .flags a{height:25px;text-decoration:none;text-align:center}header .flags a img{width:25px;border-radius:50%}header .name-and-avatar{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-content:center;align-content:center;margin-right:auto;margin-left:auto;-webkit-flex-wrap:wrap;flex-wrap:wrap}header .name-and-avatar h2{margin-bottom:10px}header .name-and-avatar .avatar{display:block;margin-left:auto;margin-right:auto;-webkit-align-self:center;align-self:center;background-color:white;border-radius:50%;border-top-left-radius:0px;border-left:30px solid white;border-right:30px solid white;border-top:15px solid white;border-bottom:40px solid white;max-width:85%;box-shadow:5px 5px 35px black}*{margin:0;padding:0;box-sizing:border-box}body{font-family:'Roboto', sans-serif;background-color:#373737;color:white;font-size:18px}a{color:white}h1,h2,h3,h4{font-family:'Jura', sans-serif}

